.media{
    align-self: center;
    justify-content: flex-end;
    display: flex;
    flex: 2;
}

.fa {
    padding: .5em;
    font-size: 1.2em;
    width: 2em;
    height: 2em;
    text-align: center;
    text-decoration: none !important;
    color: white !important;
    margin-right: 0.5em;
    margin-bottom: .25em;
    margin-top: .25em;
}

.fa:hover {
    opacity: 0.7;
}

.fa-facebook {
    background: #3B5998;
}

.fa-twitter {
    background: #55ACEE;
}

.fa-linkedin {
    background: #007bb5;
}

.fa-github {
    background: #211F1F;
}

@media screen and (max-width: 800px) {
    .media{
        background-color: white;
        align-self: center;
        display: flex;
        justify-content:center;
        width: 100%;
        position: fixed;
        bottom: 0;
    }
}