.home{
    position: relative;
    height: calc(100vh - 3em);
}

.back-home-img{
    position: absolute;
    min-width:100%;
    max-width: 200%;
    height: auto;
    min-height: 100%;
    display: block;
    bottom: 0;
    overflow-y: hidden;
    z-index: 1;
}

.over-purple{
    z-index: 2;
    position: absolute;
    background-color:var(--darkText);
    width:100vw;
    height: 100vh;
    display: block;
    bottom: 0;
    opacity: .6;
    overflow-y: hidden;
}

.hello{
    z-index: 3;
    position: absolute;
    bottom: 25vh;
    left: 10vw;
    font-size: 6vw;
    color: white;
    text-shadow: 0 0 0.2rem  black;
}

.iam{
    color: white;
    position: relative;
    display: flex;
    flex-flow: row;
    justify-content: left;
    align-items: center;
    top: -.5em;
}

.keywords{
    padding-left: .2em;
    color: white;
    font-size: 6vw;
    font-weight: bold;
}

.description{
    z-index: 4;
    color: white;
    position: absolute;
    background-color: #660166;
    opacity: .8;
    width: 100%;
    padding: 1.5em 3em .8em 3em;
    bottom: 0;
    text-align:center;
}

.tab{
    font-size: 10vw;
}

@media screen and (max-width: 800px) {
    .back-home-img img{
        height: 100vh;
        width:auto;
        display: flex;
        position: absolute;
        margin-left: 50%;
        transform: translateX(-50%);
        top: -5em;
    }
    .home{
        position: relative;
        height: calc(100vh - 2.5em);
    }
    .hello{
        top: 1em;
        font-size: 3rem;
    }
    .iam{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-top: 0.5em;
    }
    .tab{
        font-size: 3rem;
    }
    .keywords{
        font-size: 3rem;
        padding-left: 0;
    }
    .description{
        padding: 1em 2em 3.5em 2em;
        font-size: .7rem;
    }
}