.App {
  position: relative;
  top: 0;
  right: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: var(--background);
  overflow-x: hidden;
  min-height: 100vh;
}

.App-content {
  position: relative;
  z-index: 1;
  padding-top: 3em;
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

h1, h2{
  color:var(--activeText);
}

/* width */
::-webkit-scrollbar {
  width: .4em;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media screen and (max-width: 800px) {
  .App-content {
    position: relative;
    z-index: 1;
    padding-top: 2.5em;
    width: 100%;
    min-height: 100vh;
    overflow-x: hidden;
  }
}
/*
light = #fcfafc
shadow 1 = #faf2fa
shadow 3 = #f7ebf7
*/