.section-box{
    display: flex;
    flex-direction: row;
    align-self: center;
    justify-self: center;
    justify-content: space-between;
    align-items: center;
    padding: 2em;
    margin: 2.5em 3.5em 2.5em 3.5em;
    background-color: white;
    box-shadow: 0em 0em 1em var(--shadow1);
    font-size: .9rem;
}

.text-portion{
    display: flex;
    flex-direction: column;
    width: 75%;
}

.text-portion hr{
    margin-bottom: 0;
}

.image-section{
    width: auto;
    max-width:25%;
    max-height: 18vw;
    display: flex;
    justify-content: center;
}

.image-section img{
    max-width: 100%;
    max-height: 100%;
    box-shadow: 0 0 1rem var(--shadow2);
}

.date{
    margin-top: 0;
    color: gray;
}

@media screen and (max-width: 800px) {
    .section-box{
        padding: 1.5em;
        margin: 1em;
        background-color: white;
        box-shadow: 0em 0em 1em var(--shadow1);
        font-size: .9rem;
        flex-direction: column-reverse;
        align-items: stretch;
    }
    .text-portion{
        width: 100%;
    }
    .image-section{
        max-width: 100%;
        max-height: 100vw;
        display: flex;
        justify-content: center;
    }
    .image-section img{
        height: auto;
    }
}