.line{
    align-self: flex-end;
    justify-content: center;
    text-align: center;
    flex: 1;
    display: flex;
}

.line div{
    background-color: transparent;
    font-family: 'Montserrat', sans-serif;
    font-size: 1em;
    width: 7em;
    padding: .5em .5em .2em .5em;
    border-width: .15em;
}

.default:hover, .purple:hover{
    cursor: pointer;
    color: var(--hoverText);
}

.default:active, .purple:active{
    background-color:var(--active);
}

.default:focus{
    outline: none;
}

.purple{
    color: var(--activeText);
    border-bottom-style: solid;
}


.default, .purple{
    flex-direction: column;
    direction: initial;
}