.Bar{
    position: fixed;
    z-index: 2;
    font-size: 1em;
    display: flex;
    min-width: 100vw;
    min-height: 2.5em;
    background-color: white;
    padding: 0, 0.5em;
    top: 0;
    transition: top 0.3s;
    box-shadow: 0 0 1em var(--shadow1);
}

.Bar--hidden {
    top: -3em;
}

.bar-spacer{
    flex: 2;
}

@media screen and (max-width: 800px) {
    .bar-spacer{
        display: none;
    }
}