.toggle{
    display: flex;
    background-color: transparent;
    justify-content: flex-start;
}

.toggle div{
    background-color: white;
    text-align: center;
    height: 100%;
    width: 7em;
    padding: .5em 0 .5em 0;
    box-shadow: 0.2em 0.2em .5em var(--shadow1);
}

.active-btn{
    background-color:var(--background2)!important;
    color: var(--activeText);
    box-shadow: 
        inset .2em .2em .5em var(--shadow2) !important;
}
.toggle div:hover{
    cursor: pointer;
    color: var(--hoverText);
}

.toggle div:active{
    background-color: var(--active);
 }